import * as React from "react";
import IntersectionObserver from "react-intersection-observer";
import FluidImage, { FluidImageProps } from "../FluidImage";
import Placeholder from "../Placeholder";

export interface LazyFluidImageState {
  isInView: boolean;
}
export default class LazyFluidImage extends React.Component<
  FluidImageProps,
  LazyFluidImageState
> {
  state = { isInView: false };

  render() {
    const { isInView } = this.state;
    const { fillContainer, crop, color, ...rest } = this.props;
    return (
      <IntersectionObserver
        tag="span"
        onChange={() => this.setState({ isInView: true })}
        data-fluid-image
      >
        {isInView ? (
          <FluidImage fillContainer={fillContainer} crop={crop} {...rest} />
        ) : (
          <Placeholder
            fillContainer={fillContainer}
            crop={crop}
            color={color}
          />
        )}
      </IntersectionObserver>
    );
  }
}
